import "./index.css";
import {
  maxHistoryLength,
  storageHistoryKey,
  googleMapsConfig,
  openWeatherConfig,
  geoJsConfig,
} from "./utils/constants";

import GoogleMapsApi from "./components/GoogleMapApi";
import OpenWeatherApi from "./components/OpenWeatherApi";
import GeoApi from "./components/GeoApi";
import Card from "./components/Card";
import Section from "./components/Section";
import Image from "./components/Image";
import ErrorElement from "./components/ErrorElement";
import History from "./components/History";
import Form from "./components/Form";
import { getLocationWeather } from "./utils/utils";

const googleMapsApi = new GoogleMapsApi(googleMapsConfig);
const openWeatherApi = new OpenWeatherApi(openWeatherConfig);
const geoApi = new GeoApi(geoJsConfig);
const history = new History(storageHistoryKey, maxHistoryLength);
const map = new Image(".weather__map");
const errorElement = new ErrorElement(".weather__error");
const weatherSection = new Section(
  (data) => {
    const card = new Card(data, "#weather-history-item", () => {
      getLocationWeather(
        card.getCity(),
        openWeatherApi,
        googleMapsApi,
        map,
        history,
        weatherSection,
        errorElement
      );
    });
    return card.generate();
  },
  ".weather__history-list",
  maxHistoryLength
);

const weatherForm = new Form(".weather__form", () => {
  const { city } = weatherForm.getInputValues();
  getLocationWeather(
    city,
    openWeatherApi,
    googleMapsApi,
    map,
    history,
    weatherSection,
    errorElement
  );
});
weatherForm.setEventListeners();

//  
geoApi
  .getCurrentLocation()
  .then((data) =>
    getLocationWeather(
      data.city,
      openWeatherApi,
      googleMapsApi,
      map,
      history,
      weatherSection,
      errorElement
    )
  )
  .catch((err) => errorElement.showError(err.message));

weatherSection.renderItems(history.getHistory());
